import React from "react"
import { Link } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import ScrollToTop from "react-scroll-to-top"
import loadable from "@loadable/component"

const Science7Component = loadable(() => import("../components/science7Component"))

const Science7 = ({ data }) => {

  return (
    <Layout>
      <SEO
        title="7th Grade Science"
        description="Daily assignments for 7th grade science"
        image="/logo.png"
        pathname="/science7"
      />

      <ScrollToTop smooth color="#78CCC7"/>

      <section className="block-circles">
        <div className="subjects-circle circle1"
             style={{
               backgroundColor: "#fc7e7e",
               transform: "translate3d(0px, 0px, 0px) scale(0.9921, 1)",
               opacity: "0.9881"
             }}/>
        <div className="subjects-circle circle2"
             style={{ backgroundColor: "#fc7e7e", transform: "translate(0px, 0px)", opacity: 1 }}/>
        <div className="hero-wrapper pbn hero">
          <h1 className="hero intro" data-sal="slide-up"
              data-sal-duration="2000" data-sal-easing="ease">Earth Science</h1>

          <div className="hero info-block info-block-line" data-sal="slide-up"
               data-sal-duration="2000" data-sal-easing="ease"
               style={{ marginBottom: "4em" }}>
            <Link to="/seventh" className='gradeLink'>
              <h2 className="hero in-left">7th Grade</h2></Link>
          </div>
        </div>
      </section>
      
  <Science7Component/>

    </Layout>
  )
}

export default Science7